<template>
  <div class="container mt-3">
    <div class="row">
      <div class="col-md-4">
        <sidebar 
          :filter="filter" 
          :brands="brands"
          :categories="categories"
          />
      </div>
      <div class="col-md-8">
        <productsContainer 
          :products="products" 
          :totalPages="totalPages" 
          :currentPage="currentPage" 
          :totalItems="totalItems" 
          :loading="loading"
          :mainLoading="mainLoading"
        />
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import sidebar from '@/components/sidebar.vue'
import productsContainer from '@/components/productsContainer.vue'

export default {
  name: 'App',
  data () {
    return {
      products: [],
      totalItems: 0,
      currentPage: 0,
      totalPages: 0,
      loading: false,
      mainLoading: false,
      filter: {
        search: "",
        categories: [],
        brands: []
      },
      brands: ['brand-1', 'brand-2', 'brand-3'],
      categories: ['category-1', 'category-2', 'category-3']
    }
  },
  methods: {
    getItems () {
      this.loading = true
      this.currentPage += 1
      axios.get('/api/getproducts', { params: {...this.filter, page: this.currentPage}})
      .then((response) => {
        this.products.push(...response.data.items.data)
        this.totalItems = response.data.items.total
        this.totalPages = response.data.items.last_page
        this.loading = false
        this.mainLoading = false
      })
    },
    changeFilter () {
      this.currentPage = 0
      this.products=[]
      this.mainLoading = true
      this.getItems()
    }
  },
  mounted () {
    this.mainLoading = true
    this.getItems()
  },
  watch: {
    filter: {
      deep: true,
      handler(){
        this.changeFilter()
      }
    }
  },
  components: {
    sidebar,
    productsContainer
  }
}
</script>

<style>
body{
  background: #eee;
}
</style>
